import React, { Component } from "react"
import { Link } from "react-router-dom"
import Login from "../components/Login"
import SpinnerLoading from "../components/SpinnerLoading"
import * as firebase from "firebase"
import FichaCliente from "../components/FichaCliente"
import "./Styles/ProovedoresList.css"

class ClientesList extends Component {
    state = {
        search: undefined,
        user: undefined,
        loading: true,
        error: null,
        cliente: undefined,
        recuperandoUsuario: true,
        clientes: [],
    }
    constructor(props) {
        const firebaseConfig = {
            apiKey: "AIzaSyDPmTjxjMxN2abofP8ZczgFGeYQYSvbXHE",
            authDomain: "stock-119e8.firebaseapp.com",
            databaseURL: "https://stock-119e8.firebaseio.com",
            projectId: "stock-119e8",
            storageBucket: "stock-119e8.appspot.com",
            messagingSenderId: "1007277754269",
            appId: "1:1007277754269:web:d1db0def0da907b2"
        }
        if (!firebase.apps.length) {
            firebase.initializeApp(firebaseConfig);
        }
        super(props)
    }
    async fetchData() {
        const productosRef = await firebase.database().ref().child('clientes2')
        productosRef.on("value", snapshot => {
            console.log(snapshot.val())
            this.setState({
                clientes: snapshot.val(),
                loading: false
            })
        })
    }
    async componentDidMount() {
        await this.authListener()
        this.fetchData()
        if (this.props.location.search) {
            this.setState({
                cliente: (this.props.location.search).slice(1, this.props.location.search.length)
            }
            )
        }
    }
    handleClick(cliente) {
        this.setState({
            cliente: cliente
        })
    }
    logOut() {
        firebase.auth().signOut()
    }
    async authListener() {
        await firebase.auth().onAuthStateChanged((user) => {
            console.log(user)
            if (user) {
                this.setState({
                    user: user,
                    recuperandoUsuario: false
                })
            }
            else {
                this.setState({
                    user: null,
                    recuperandoUsuario: false
                })
            }
        })
    }
    returnBack() {
        console.log("return back")
        this.setState({
            cliente: undefined
        })

    }
    handleChange(e) {
        var forEach = Array.prototype.forEach;
        var arrayClientes = document.getElementsByClassName("btn-cliente")
        console.log(arrayClientes)
        forEach.call(arrayClientes, function (f) {
            if (f.innerHTML.toLowerCase().search(e.target.value.toLowerCase()) == -1)
                f.parentNode.parentNode.style.display = "none";
            else
                f.parentNode.parentNode.style.display = "block";
        });
    }
    render() {
        if (this.state.recuperandoUsuario) {
            return (
                <>
                    <SpinnerLoading />
                </>
            )
        }
        else {
            try {
                const user = this.state.user.email
                if (user) {
                    if (this.state.loading) {
                        return (
                            <div>
                                <SpinnerLoading />
                            </div>
                        )
                    }
                    else {
                        if (this.state.cliente) {
                            const cliente = this.state.clientes[this.state.cliente]
                            if (cliente) {
                                return (
                                    <div>
                                        <FichaCliente proovedor={cliente} seccion="clientes" handleClick={e => {
                                            this.returnBack()
                                        }} />
                                    </div>
                                )
                            }
                            else {
                                return (
                                    <div>
                                        <div className="productList">
                                            <div className="container">
                                                <div className="row">
                                                    <div className="col text-center">
                                                        <h1 className="title">No existe el Cliente</h1>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                )
                            }
                        }
                        else {
                            return (
                                <div>
                                    <div className="productList">
                                        <div className="container-fluid">
                                            <div className="row">
                                                <div className="col-6 offset-3">
                                                    <form>
                                                        <div className="formRow">
                                                            <div className="form-group col-12 text-center">
                                                                <input type="text" className="form-control" onChange={
                                                                    e => {
                                                                        this.handleChange(e)
                                                                    }
                                                                } placeholder="Buscar Cliente" />
                                                            </div>

                                                        </div>
                                                    </form>
                                                </div>
                                                <div className="col">
                                                    <Link to="/NuevoCliente">
                                                        <button type="button" className="btn btn-success">
                                                            Agregar Nuevo Cliente
                                                        </button>
                                                    </Link>
                                                </div>
                                            </div>
                                            <div className="row">
                                                {Object.values(this.state.clientes).map(cliente => (
                                                    <div className="col-12 col-md-6 col-lg-4 text-center form-group">
                                                        <Link to={{
                                                            pathname: `/clientes/`,
                                                            search: `${cliente.datos.nombre}`
                                                        }}>
                                                            <button type="button" className="btn btn-outline-light btn-cliente" onClick={() => {
                                                                this.handleClick(cliente.datos.nombre)
                                                            }}>
                                                                {cliente.datos.nombre}
                                                            </button>
                                                        </Link>
                                                    </div>
                                                ))}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            )

                        }
                    }
                }
            }
            catch{
                return (
                    <Login redirect="Clientes" />
                )
            }
        }
    }
}
export default ClientesList